import React, { Component } from 'react';
import Fetch from 'react-fetch-component';

export default class ListPadel extends Component {
  render() {

    const url = `/api/padel`;
    console.log('url', url);

    return (
      <div>
        <h1>Padel</h1>
        <Fetch url={url} >
          {({ loading, data, error }) => (
            <div>
              {loading && <span>Loading...</span>}
              {console.log("data", data)}
              <ul>
                {data && data.map((val, i) => {
                  return <div class="card text-center">
                    <div class="card-header"><span class="badge bg-primary">{val.date}</span>{val.weekday}</div>
                    <div class="card-body">
                      <h5 class="card-title">{val.name}</h5>
                      <p class="card-text">{val.address}</p>
                    </div>
                    <div class="card-footer text-muted">{val.duration + '' + val.cost}</div>
                  </div>
                })}
              </ul>
            </div>
          )}
        </Fetch>
      </div>
    );
  }
}